* {
  box-sizing: border-box;
}
body {
  background-color: #17181f;
  color: #ddd;
}
.App {
  max-width: 360px;
  margin: 30px auto;
}
h1 {
  text-align: center;
  color: #fff;
  margin: 0;
  font-size: 2.5rem;
}
h1 span {
  font-size: 1rem;
}
form {
  margin-top: 20px;
}
form input {
  width: 100%;
  background-color: transparent;
  color: #ddd;
  border: 2px solid #30313d;
  padding: 2px 5px;
  border-radius: 5px;
}
form input[type="datetime-local"] {
  color: #777;
}
form div.basic {
  display: flex;
  gap: 5px;
  margin-bottom: 5px;
}
form button {
  width: 100%;
  border: 0;
  margin-top: 5px;
  border-radius: 5px;
  padding: 5px;
  background-color: #ddd;
}
form button:hover {
  background-color: #fff;
  cursor: pointer;
}
div.transactions {
  margin-top: 15px;
}
div.transaction {
  display: flex;
  justify-content: space-between;
  padding: 5px 0;
  border-top: 1px solid #30313d;
}
div.transaction:first-child {
  border-top: 0;
}
div.transaction div.name {
  font-size: 1.1rem;
}
div.transaction div.description {
  font-size: 0.7rem;
  color: #888;
}
div.right {
  text-align: right;
}
div.price {
  font-size: 1.1rem;
}
div.price.green {
  color: #1c1;
}
div.price.red {
  color: #c11;
}
div.transaction div.datetime {
  font-size: 0.7rem;
  color: #888;
}
